/* <ZERO STYLE> ============================== */
@import "nullstyle.scss";
/* </ZERO STYLE> ============================= */

/* <IMPORT FONTS> ============================ */
@font-face {
	font-family: 'Montserrat'; 
    font-style: normal;
    font-weight: 400;
	src: url(../fonts/Montserrat/Montserrat-Regular.ttf); 
}

@font-face {
	font-family: 'Montserrat'; 
    font-style: normal;
    font-weight: 500;
	src: url(../fonts/Montserrat/Montserrat-Medium.ttf); 
}

@font-face {
	font-family: 'Montserrat'; 
    font-style: normal;
    font-weight: 600;
	src: url(../fonts/Montserrat/Montserrat-SemiBold.ttf); 
}

@font-face {
	font-family: 'feijoa'; 
    font-style: normal;
    font-weight: 500;
	src: url(../fonts/test-feijoa-font/TestFeijoa-Medium.otf); 
}

@font-face {
	font-family: 'bebas_kai'; 
  font-style: normal;
  font-weight: 400;
	src: url(../fonts/bebas_kai/BebasKai.ttf); 
}

@font-face {
	font-family: 'proxima_nova'; 
  font-style: normal;
  font-weight: 400;
	src: url(../fonts/proxima-nova/proxima-nova-regular.ttf); 
}

@font-face {
	font-family: 'proxima_nova'; 
  font-style: normal;
  font-weight: 600;
	src: url(../fonts/proxima-nova/proxima-nova-semibold.ttf); 
}
/* </IMPORT FONTS> =========================== */

/* <FONT BODY> =============================== */
$fontFamily: "Montserrat";
/* </FONT BODY> ============================== */

/* <VARS> ==================================== */
$minWidth: 320px;
$maxWidth: 1920px;
$maxWidthContainer: 1780px;
$tb: 991.98px;
$mb: 767.98px;
$smb: 479.98px;
/* </VARS> =================================== */

// <CONTAINER SITE> =============================== */
.container {
  max-width: $maxWidthContainer;
  margin: 0 auto;
  padding: 0 10px;
  @media (min-width: 1440px) {
    max-width: 1350px;
  }
  @media (max-width: 1180px) {
    max-width: 1075px;
    
  }
  /*@media (max-width: 1080px) {
    max-width: 1056px;
  }*/
  @media (max-width: 728px) {
    max-width: 705px;
  }
  @media (max-width: 375px) {
    max-width: 320px;
  }
}
// </CONTAINER SITE> =============================== */

// <BORDER GRADIENT> =================================
.top {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, rgba(41, 193, 214, 0) 0%, rgba(41, 193, 214, 0.26) 69.1%, rgba(133, 255, 234, 0.39) 100%);

}

.bottom {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, rgba(41, 193, 214, 0) 0%, rgba(41, 193, 214, 0.26) 69.1%, rgba(133, 255, 234, 0.39) 100%);

}

.left {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  height: auto;
  max-height: 100%;
  width: 2px;
  background: linear-gradient(270deg, rgba(41, 193, 214, 0) 0%, rgba(41, 193, 214, 0.26) 69.1%, rgba(133, 255, 234, 0.39) 100%);
}

.right {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  height: auto;
  max-height: 100%;
  width: 2px;
  background: linear-gradient(270deg, rgba(41, 193, 214, 0) 0%, rgba(41, 193, 214, 0.26) 69.1%, rgba(133, 255, 234, 0.39) 100%);
}

// </BORDER GRADIENT> =================================

// <STYLE TITLE> ======================================
.title {
  display: inline-block;
  background: radial-gradient(100% 460.9% at 100% 100%, rgba(206, 101, 255, 0) 0%, #FF8888 100%), #FFFFFF;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  font-family: "bebas_kai";
  font-weight: 400;
  font-size: 96px;
  line-height: 1;
  text-transform: uppercase;
  &.green {
    background: radial-gradient(100% 1111.91% at 100% 50%, #B2F6FF 0%, rgba(38, 166, 141, 0) 50%, #B2F6FF 100%), #FFFFFF;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
  }
  @media (max-width:729px) {
    font-size: 86px;
  }
  @media (max-width:375px) {
    font-size: 64px;
  }
}
// </STYLE TITLE> =====================================

// <STYLE BUTTON> =====================================
.btn {
  display: inline-block;
  background: linear-gradient(270deg, rgba(51, 134, 120, 0.65) 0%, rgba(41, 193, 214, 0.065) 35.55%, rgba(41, 193, 214, 0.065) 61.81%, rgba(51, 134, 120, 0.65) 100%);
  box-shadow: inset 0px 0px 16px rgb(73 214 171 / 40%);
  text-transform: uppercase;
  color: #FFFFFF;
  text-align: center;
  cursor: pointer;
}
// </STYLE BUTTON> =====================================

body {
  font-family: $fontFamily, sans-serif;
}

.header {
  background-image: url(../img/header/Union.svg), url(../img/header/Background.png);
  background-position: right 50.5% top, center bottom;
  background-size: auto 175px, cover;
  background-repeat: no-repeat, no-repeat;
  height: 100vh;
  position: relative;
  z-index: 1;
  min-height: 1200px;
  @media (max-width: 852px) {
    background-image: url(../img/header/header__top/header__top2.png), url(../img/header/header__top/Union.svg), url(../img/header/Background.png);
    background-position: right 51% top, 100% -8%, center bottom;
    background-size: auto 175px, contain, cover;
    background-repeat: no-repeat, no-repeat, no-repeat;
  }
  @media (max-width: 580px) {
    background-position: right 49% top, 100% -8%, center bottom;
    background-size: 100% 175px, contain, cover;
  }
  @media (max-width: 385px) {
    background-position: right 49% top, 100% -8%, center bottom;
    background-size: 380px 175px, contain, cover;
  }
  &__top {
    display: flex;
    align-items: center;
    height: 120px;
    position: relative;
    z-index: 1;
    backdrop-filter: blur(10px);
  }
  &__item {
    &:first-child, &:nth-child(3) {
      flex: 0 1 240px;
      @media (max-width: 852px) {
        flex: 0 1 195px;
      }
    }
    &:first-child {
      padding-left: 60px;
      position: relative;
      @media (max-width: 580px) {
        padding-left: 15px;
      }
    }
    &:nth-child(2) {
      flex: 1 1 auto;
      height: 100%;
      position: relative;
    }
    &:nth-child(3) {
    }
    &:nth-child(4) {
      position: absolute;
      top: 125px;
      left: 0;
      z-index: 1;
    }
    &:nth-child(5) {
      position: absolute;
      bottom: 0;
      right: 0;
      z-index: 1;
      width: 355px;
      height: 1px;
      background: linear-gradient(270deg, rgba(38, 166, 154, 0) 0%, #B2F6FF 100%);
      transform: matrix(-1, 0, 0, 1, 0, 0);
    }
    &:nth-child(6) {
      position: absolute;
      bottom: 1px;
      left: 0;
      z-index: 1;
      width: 355px;
      height: 1px;
      background: linear-gradient(270deg, rgba(38, 166, 154, 0) 0%, #B2F6FF 100%);
    }
  }
  &__menu {
    width: 59px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &-line {
      display: none;
    }
  }
  &__logo {
    position: absolute;
    bottom: -25px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 5;
    display: flex;
    flex-direction: column;
    align-items: center;
    @media (max-width: 853px) {
      width: 142px;
      bottom: -17px;
    }
    &-png img{
      width: 100px;
      height: 100px;
      @media (max-width: 853px) {
        width: 93px;
        height: 93px;
      }
    }
    &-text {
      margin-top: 10px;
      /*@media (max-width: 853px) {
        margin-top: 14px;
      }*/
      span {
        font-family: 'feijoa'; 
        font-size: 16px;
        line-height: 140%;
        /* identical to box height, or 18px */
        text-align: center;
        letter-spacing: 0.05em;
        color: #EDFFFE;
        text-shadow: 0px 0px 4.67213px rgba(38, 166, 154, 0.15), 0px 0px 1.86885px rgba(38, 166, 154, 0.25);
        @media (max-width: 853px) {
          font-size: 12px;
        }
        @media (max-width: 512px) {
          font-size: 12px;
        }
        
        @media (max-width: 428px) {
          font-size: 10px;
        }
      }
    }
  }
  &__btn {
    &-border {
      display: inline-block;
      position: relative;
    }
    @media (max-width: 853px) {
      display: flex;
      justify-content: flex-end;
      margin-right: 30px;
    }
    @media (max-width: 580px) {
      margin-right: 15px;
    }
    @media (max-width: 375px) {
      display: none;
    }
    a {
      font-size: 12px;
      line-height: 15px;
      /* identical to box height */
      letter-spacing: 0.03em;
      padding: 12px 24px;
      @media (max-width: 953px) {
        margin-right: 60px;
      }
      @media (max-width: 853px) {
        font-size: 10px;
        margin-right: 0;
      }
      @media (max-width: 580px) {
        padding: 7px 10px;
        font-size: 8px;
      }
    }
  }
}

.side-bar {
  position: absolute;
  top: -5px;
  left: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: calc(100vh - 130px);
  background: linear-gradient(331.73deg, rgba(0, 0, 0, 0.1) -1.55%, #000000 115.15%);
  box-shadow: 10px 0px 24px rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(30px);
  width: 355px;
  padding: 45px 60px 0 45px;
  transform: translateY(-200%);
  /* Note: backdrop-filter has minimal browser support */
  transition: all .5s;
  &.active {
    transform: translateY(0);
  }
  &-top {
    h5 {
      margin-bottom: 25px;
      font-weight: 500;
      font-size: 24px;
      line-height: 29px;
      text-transform: uppercase;
      background: linear-gradient(0deg, #26A69A -31.82%, #B2F6FF 110.8%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      text-fill-color: transparent;
      text-shadow: 0px 0px 5px rgba(38, 166, 154, 0.9), 0px 0px 10px rgba(38, 166, 154, 0.45);
    }
  }
  &-link {
    position: relative;
    display: block;
    margin-bottom: 10px;
    .symbol {
      width: 6px;
      height: 6px;
      background: radial-gradient(95.83% 95.83% at 37.5% 4.17%, #7DFFE0 0%, rgba(99, 255, 218, 0) 100%);
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    p {
      margin-left: 12px;
      font-weight: 400;
      font-size: 21px;
      color: #FFFFFF;
    }
    &:hover {
      .symbol {
        width: 2px;
        height: 40px;
        background: linear-gradient(180deg, rgba(99, 255, 218, 0) 0%, #7DFFE0 45.74%, rgba(99, 255, 218, 0) 100%);
        border-radius: 1px;
      }
      p {
        font-weight: 600;
      }
    }
  }
  &-bottom {
    display: flex;
    align-items: center;
    &__item {
      display: flex;
      align-items: center;
      height: 100px;
      position: relative;
      z-index: 10;
      margin-left: 13px;
      &:first-child {
        margin-left: 0;
      }
      &:after {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        left: 0;
        right: 0;
        filter: blur(9px);
        background: linear-gradient(180deg, rgba(178, 246, 255, 0) 0%, #26A69A 100%);
        z-index: -1;
      }
      .icon__svg {
        &:hover {
          fill: rgba(255, 255, 255, 0.01);
          border: 1px solid #CAFFFA;
          box-shadow: 0px 0px 5.57692px rgba(0, 0, 0, 0.5), 0px 0px 11.1538px rgba(0, 0, 0, 0.25), inset 0px 0px 5.57692px rgba(0, 0, 0, 0.5), inset 0px 0px 11.1538px rgba(0, 0, 0, 0.25);
        }
      }
    }
  }
}

.history {
  background-color: #000;
  position: relative;
  height: 900px;
}

.star {
  position: absolute;
  width: 1px;
  height: 1px;
  background-color: white;
}

/* Set the animation, color, size and hide the text */
.intro {
  position: absolute;
  top: 30%;
  left: 35%;
  z-index: 1;
  animation: intro 6s ease-out 1s;
  color: rgb(75, 213, 238);
  font-weight: 400;
  font-size: 300%;
  opacity: 0;
}

@keyframes intro {
  0% {
      opacity: 0;
  }
  20% {
      opacity: 1;
  }
  90% {
      opacity: 1;
  }
  100% {
      opacity: 0;
  }
}

