.content__text {
color: #FFFF82;
padding-bottom: 25px;
}

/* Set the font, lean the board, position it */
#board {
font-family: Century Gothic, CenturyGothic, AppleGothic, sans-serif;
transform: perspective(300px) rotateX(25deg);
transform-origin: 50% 100%;
text-align: justify;
position: absolute;
margin-left: -9em;
font-weight: bold;
overflow: hidden;
font-size: 350%;
height: 50em;
width: 18em;
bottom: 0;
left: 50%;
}

#board:after {    
position: absolute;
content: ' ';
bottom: 60%;
left: 0;
right: 0;
top: 0;
}

/* Set the scrolling animation and position it */
#content {
animation: scroll 100s linear 5s;
position: absolute;
top: 100%;
}

#title, #subtitle {
text-align: center;
}

@keyframes scroll {
  0% {
    top: 100%;
  }
  100% {
    top: -170%;
  }
}

.mission {
  padding-bottom: 100px;
  background: black;
  padding-top: 50px;
  &__red {
    display: block;
    background: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(255, 0, 0, 0.2), 0px 0px 20px rgba(255, 57, 57, 0.4), 0px 0px 15px rgba(226, 42, 42, 0.6), 0px 0px 10px rgba(219, 47, 47, 0.8), 0px 0px 5px #D02B2B, inset 0px 0px 6px rgba(189, 22, 22, 0.6), inset 0px 0px 4px rgba(255, 63, 63, 0.8), inset 0px 0px 2px #FF1515;
    border-radius: 20px;
    width: 1440px;
    height: 14px;
    margin: 0 auto 30px auto;
    overflow: hidden;
    margin-bottom: 120px;
  }
  &__inner {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__item {
    flex: 1 0 50%;
    @media (max-width: 1024px) {
      flex: 1 0 100%;
    }
  }
  &__img {
    display: block;
    padding: 0px 0px 80% 0px;
  }
  &__our {
    padding: 0 10px;
    @media (max-width: 1024px) {
      margin-top: 44px;
    }
    @media (max-width: 375px) {
      padding: 0;
    }
    &-title {
      padding-top: 8px;
      position: relative;
      margin-bottom: 24px;
      &::after {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 4px;
        left: 0px;
        top: 0px;
        background: radial-gradient(100% 460.9% at 100% 100%, rgba(206, 101, 255, 0) 0%, #FF8888 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
        border-radius: 3px;
      }
      &-red {
        position: relative;
        padding-bottom: 8px;
        @media (max-width: 540px) {
          font-size: 78px;
        }
        @media (max-width: 416px) {
          font-size: 70px;
        }
        @media (max-width: 375px) {
          font-size: 64px;
        }
        &::before {
          content: '';
          display: block;
          position: absolute;
          width: 100%;
          height: 4px;
          left: 0px;
          bottom: 0px;
          background: radial-gradient(100% 460.9% at 100% 100%, rgba(206, 101, 255, 0) 0%, #FF8888 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
          border-radius: 3px;
        }
      }
    }
    &-text {
      font-family: 'proxima_nova'; 
      font-weight: 400;
      font-size: 20px;
      line-height: 27px;
      /* or 135% */
      color: #FFFFFF;
      text-align: justify;
    }
  }
}

.secret {
  padding-top: 50px;
  padding-bottom: 100px;
  background: black;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    margin-bottom: 24px;
    h2 {
      padding: 4px 0;
      position: relative;
      &::after {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 4px;
        left: 0px;
        top: 0px;
        background: linear-gradient(270deg, rgba(38, 166, 136, 0) 0%, #B2F6FF 49.98%, rgba(38, 166, 136, 0) 100%);
        border-radius: 3px;
      }
    }
    &-inner {
      position: relative;
      &::before {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 4px;
        left: 0px;
        bottom: 0px;
        background: linear-gradient(270deg, rgba(38, 166, 136, 0) 0%, #B2F6FF 49.98%, rgba(38, 166, 136, 0) 100%);
        border-radius: 3px;
      }
    }
  }
  &__text {
    margin-bottom: 40px;
    p {
      font-family: 'proxima_nova'; 
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 27px;
      color: #FFFFFF;
      text-align: center;
    }
  }
  &__box {
    display: flex;
    justify-content: center;
    gap: 24px;
    max-width: 800px;
    margin: 0 auto;
  }
  &__column {
    width: 250px;
    min-height: 345px;  
    display: flex;
    flex-direction: column;
    align-items: center;
    &-object {
      height: 140px;
      display: flex;
      align-items: flex-end;
      margin-bottom: 20px;
    }
    &-subtitle {
      font-family: 'proxima_nova'; 
      font-style: normal;
      font-weight: 600;
      color: #fff;
      font-size: 20px;
      line-height: 24px;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      text-align: center;
    }
    &-bg {
      background-size: cover;
      background-position: top center;
      background-repeat: no-repeat;
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: 250px;
      width: 100%;
      &.bg1 {
        background-image: url(../img/secret/01.png);
        height: 375px;
      }
      &.bg2 {
        background-image: url(../img/secret/02.png);
        height: 300px;      
      }
      &.bg3 {
        background-image: url(../img/secret/03.png);
        height: 375px;
      }
    }
  }
