.algorithm {
  padding-top: 50px;
  padding-bottom: 100px;
  background: black;
  &__inner {
    max-width: 1280px;
    margin: 0 auto;
  }
  &__title {
    max-width: 1070px;
    margin-bottom: 54px;
    @media (max-width:1180px) {
      max-width: 675px;
    }
    &-top {
      span {
        display: inline-block;
        width: 100%;
        font-family: 'Montserrat'; 
        font-style: normal;
        font-weight: 600;
        font-size: 26px;
        line-height: 32px;
        letter-spacing: 0.04em;
        background: radial-gradient(106.02% 485.88% at 106.02% 50%, #B2F6FF 0%, #26A69A 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        position: relative;
        &::after {
          content: '';
          display: block;
          position: absolute;
          max-width: 925px;
          width: 100%;
          height: 4px;
          right: 0px;
          top: 50%;
          transform: translateY(-50%);
          background: radial-gradient(100% 1111.91% at 100% 50%, rgba(38, 166, 136, 0) 0%, #B2F6FF 100%);
          border-radius: 3px;
        }
        @media (max-width:1180px) {
          &::after {
            max-width: 530px;
          }
        }
        @media (max-width:729px) {
          &::after {
            max-width: 252px;
            left:145px;
          }
        }
        @media (max-width:375px) {
          &::after {
            max-width: 185px;
          }
        }
      }
    }
    &-text {
      position: relative;
      padding-bottom: 8px;
      &::after {
        content: '';
        display: block;
        position: absolute;
        max-width: 925px;
        width: 100%;
        height: 4px;
        left: 0;
        bottom: 0;
        background: radial-gradient(100% 1111.91% at 100% 50%, rgba(38, 166, 136, 0) 0%, #B2F6FF 100%);
        border-radius: 3px;
      }
      @media (max-width:1180px) {
        &::after {
          max-width: 367px;
        }
      }
      @media (max-width: 540px) {
        font-size: 75px;
      }
      @media (max-width: 375px) {
        font-size: 64px;
      }
    }
  }
  &__text {
    margin: 40px 0;
    p {
      font-family: 'proxima_nova';
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 27px;
      /* or 150% */
      color: #FFFFFF;
      &:not(:last-child) {
        margin-bottom: 24px;
      }
    }
  }
  &__img {
    &-bc {
      min-height: auto;
      display: block;
      padding: 0px 0px 75% 0px;
      &:last-child {
        display: none;
      }
      @media (max-width: 375px) {
        &:first-child{
          display: none;
        }
        &:last-child {
          display: block;
          padding: 0px 0px 404% 0px;
        }
      }
    }
  } 
}

.gradiation {
  padding: 150px 0 150px 0;
  background: #000;
  @media (max-width: 1440px) {
    padding: 125px 0 120px 0;
  }
  @media (max-width: 1180px) {
    padding: 50px 0 100px 0;
  }
  @media (max-width: 1024px) {
    padding: 0 0 50px 0;
  }
  &__inner {
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: 1024px) {
      flex-wrap: wrap;
    }
  }
  &__item {
    flex: 1 0 50%;
    &:first-child {
      margin-right: -140px;
    }
    @media (max-width: 1024px) {
      flex: 1 0 100%;
      &:first-child {
        margin-right: 0;
      }
    }
  }
  &__img {
    display: block;
    padding: 0 0 113% 0;
  }
  &__content {
    display: flex;
    flex-direction: column;
  }
  &__title {
    padding-top: 8px;
    margin-bottom: 24px;
    position: relative;
    max-width: 407px;
    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 4px;
      left: 0px;
      top: 0px;
      background: linear-gradient(270deg, rgba(38, 166, 136, 0) 0%, #B2F6FF 49.98%, rgba(38, 166, 136, 0) 100%);
      border-radius: 3px;
    }
    &-top {
      padding-bottom: 8px;
      &::after {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 4px;
        left: 0px;
        bottom: 0px;
        background: linear-gradient(270deg, rgba(38, 166, 136, 0) 0%, #B2F6FF 49.98%, rgba(38, 166, 136, 0) 100%);
        border-radius: 3px;
      }
    }
  }
  &__text {
    font-family: 'proxima_nova';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    /* or 135% */
    color: #FFFFFF;
    &:not(:last-child) {
      margin-bottom: 32px;
    }
  }
}

.form {
  padding: 33px 40px 29px 40px;
  max-width: 546px;
  width: 100%;
  display: flex;
  justify-content: center;
  background: radial-gradient(112.5% 177.78% at 112.5% 113.13%, rgba(101, 255, 255, 0.4165) 0%, rgba(101, 181, 255, 0.2125) 68.43%, rgba(114, 123, 199, 0.085) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
  box-shadow: 10px 0px 24px rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(30px);
  position: relative;
  @media (max-width: 1024px) {
    display: none;
  }
  &.form2 {
    display: none;
    @media (max-width: 1024px) {
      display: block;
      max-width: 530px;
      margin: 0 auto;
    }
  }

  form {
    max-width: 466px;
    width: 100%;
  }
  &__title {
    width: 282px;
    position: relative;
    margin: 0 auto 24px auto;
    @media (max-width: 375px) {
      width: 207px;
    }
    h2 {
      padding-bottom: 8px;
      font-size: 48px;
      line-height: 1.17;
      text-align: center; 
      @media (max-width: 375px) {
        font-size: 38px;
      }
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 4px;
      left: 0px;
      bottom: 0px;
      background: linear-gradient(270deg, rgba(38, 166, 136, 0) 0%, #B2F6FF 49.98%, rgba(38, 166, 136, 0) 100%);
      border-radius: 3px;
    }
  }
  &__box {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }
  &__input {
    background: linear-gradient(270deg, rgba(41, 193, 214, 0) 0%, rgba(41, 193, 214, 0.26) 69.1%, rgba(133, 255, 234, 0.39) 100%);
    font-family: 'proxima_nova';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.22;
    /* identical to box height */
    color: #FFFFFF;
    padding: 8px 12px;
  }
  &__border {
    position: relative;
  }
  &__btn {
    width: 121px;
    padding: 12px 24px;
  }
}

